@import 'src/Resource/scss/lib.scss';
$main_blue: #4782E0;
$main_orange: #F76659;
$main_white: #F6F4F3;
$main_dark: #121f35;
$main_def: #fff;

@font-face {
  font-family: CusRange;
  src: local("微軟正黑體");
  unicode-range: U+4E00-9FFF;
}
@font-face {
  font-family: CusRange;
  src: local("Century Gothic");
  unicode-range: U+00-024F;
}

html, body {
  background-color: $main_dark !important;
  font-family: CusRange, Sans-serif !important;
}
a {
  color: inherit !important;
  text-decoration: none !important;
  cursor: pointer !important;
}
.App {
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
  padding: 0 4.5rem;
  color: $main_def;
  @include rwd(mobile) {
    padding: 0 2rem;
  }
}

// public
.w100 {
  width: 100%;
}
.h100 {
  height: 100%;
}
.text_center {
  text-align: center;
}
.text_important {
  color: $main_orange !important;
}
.text_delete {
  text-decoration: line-through !important;
}