@import 'src/Resource/scss/lib.scss';
$main_blue: #4782e0;
$main_orange: #f76659;
$main_white: #f6f4f3;
$main_dark: #121f35;
$main_peach: #f52f57;
.wrap {
  // position: relative;
  width: 100%;
  .info {
    position: relative;
    width: 100%;
    height: 100vh;
    // padding: 4rem 0;
    @include flex();
    @include box();
    @include align(center);
    @include justify(space-around);
    @include rwd(mobile) {
      padding: 0;
      @include flex(column-reverse);
      @include justify(center);
    }
    > div {
      position: relative;
      z-index: 2;
    }
    .info__title {
      font-size: 10vw;
      position: relative;
      color: $main_orange;
      text-shadow: 5px 5px 2px rgba(#666, 0.7);
      letter-spacing: 0.2vw;
      line-height: 1;
      user-select: none;
      @include rwd(mobile) {
        font-size: 18vw;
      }
    }
    .info__subtitle {
      font-size: 2vw;
      font-weight: 600;
      letter-spacing: 0.1vw;
      color: $main_blue;
      line-height: 1;
      margin-bottom: -1vw;
      user-select: none;
      text-align: right;
      margin-right: 6px;
      @include rwd(mobile) {
        font-size: 4vw;
      }
    }
    .info__photo {
      position: relative;
      img {
        max-height: 26vw;
        @include rwd(mobile) {
          max-height: 35vh;
          border-radius: 50%;
          transform: rotate(10deg);
          padding: 2rem 0;
        }
      }
      .info__photo__frame {
        @include box();
        border-radius: 10px;
        height: 27vw;
        width: 105%;
        position: absolute;
        bottom: 0;
        right: 0;
        border: 6px solid $main_blue;
        z-index: 2;
        animation: flicker 4s 0s infinite;
        &::after {
          position: absolute;
          top: -6px;
          left: -6px;
          content: '';
          border: 6px solid $main_blue;
          filter: blur(10px);
          display: block;
          width: 100%;
          height: 100%;
          box-sizing: content-box;
        }
        @include rwd(mobile) {
          display: none;
        }
      }
      .info__photo__frame2 {
        @include box();
        border-radius: 10px;
        height: 27vw;
        width: 105%;
        position: absolute;
        top: 0;
        left: 0;
        border: 6px solid $main_orange;
        z-index: 1;
        animation: flicker2 4s 0s infinite;
        &::after {
          position: absolute;
          top: -6px;
          left: -6px;
          content: '';
          border: 6px solid $main_orange;
          filter: blur(10px);
          display: block;
          width: 100%;
          height: 100%;
          box-sizing: content-box;
        }
        @include rwd(mobile) {
          display: none;
        }
      }
    }
    .tooltip {
      position: absolute;
      top: -1vh;
      left: -10vw;
      margin-left: 20px;
      transform: translate(0, -50%);
      padding: 10px;
      color: #444444;
      background-color: #eeeeee;
      font-weight: normal;
      font-size: 13px;
      border-radius: 8px;
      position: absolute;
      z-index: 99;
      box-sizing: border-box;
      box-shadow: 0 1px 8px rgba(0, 0, 0, 0.5);
      opacity: 0;
      @include rwd(laptop) {
        display: none;
      }
      &.tooltip_show {
        opacity: 1;
      }
      .tooltip__content {
        margin: 0;
        font-size: 2vw;
        user-select: none;
      }
      i {
        position: absolute;
        top: 50%;
        left: 100%;
        margin-top: -12px;
        width: 12px;
        height: 24px;
        overflow: hidden;
        &::after {
          content: '';
          position: absolute;
          width: 12px;
          height: 12px;
          right: -50%;
          top: 50%;
          transform: translate(-100%, 50%) rotate(45deg);
          background-color: #eeeeee;
          box-shadow: 0 1px 8px rgba(0, 0, 0, 0.5);
        }
      }
    }
    .bg {
      @include flex();
      @include align(center);
      @include justify(center);
      @include box();
      width: 100%;
      height: 100%;
      position: absolute !important;
      top: 0;
      left: 0;
      z-index: 1;
      overflow: hidden;
      & > div {
        .parallax-outer {
          height: 100%;
          width: 100%;
          @include flex();
          @include align(center);
          @include justify(center);
          .parallax-inner {
            @include flex();
            @include align(center);
            width: 100%;
            height: 50%;
            font-size: 10rem;
            color: rgba(#fff, 0.3);
            svg {
              // width: 100%;
              height: 100%;
              path {
                fill: none;
                stroke: rgba(#fff, 0.2);
              }
            }
          }
        }
      }
      &__left {
        flex: 1;
        height: 100%;
        overflow: hidden;
        .parallax-inner {
          transform: translateX(5rem);
          @include justify(flex-end);
        }
      }
      &__right {
        flex: 1;
        height: 100%;
        overflow: hidden;
        .parallax-inner {
          @include justify(flex-start);
        }
      }
    }
  }
  .mouse {
    position: absolute !important;
    bottom: 20px;
    left: 0;
    right: 0;
    margin: auto;
    width: 20px;
    height: 40px;
    border-radius: 10px;
    border: 1px solid $main_white;
    @include rwd(mobile) {
      width: 4px;
      height: 20px;
      bottom: 60px;
      background-color: $main_white;
      animation: arrowScroll 2s 0s infinite;
    }
    &::before {
      position: absolute;
      top: 5px;
      left: 0;
      right: 0;
      margin: auto;
      content: '';
      border-radius: 10px;
      width: 4px;
      height: 8px;
      background-color: $main_white;
      animation: mouseScroll 2s 0s infinite;
      @include rwd(mobile) {
        animation: mouseScrollStatic 2s 0s infinite;
        top: auto;
        bottom: 0;
        left: 0px;
        right: auto;
        width: 4px;
        height: 16px;
        transform: rotate(-45deg);
        transform-origin: bottom right;
      }
    }
    &::after {
      position: absolute;
      margin: auto;
      border-radius: 10px;
      width: 4px;
      height: 8px;
      background-color: $main_white;
      @include rwd(mobile) {
        content: '';
        top: auto;
        bottom: 0;
        left: auto;
        right: 0px;
        width: 4px;
        height: 16px;
        transform: rotate(45deg);
        transform-origin: bottom left;
      }
    }
  }
  .contain {
    padding: 3rem 0;
    transition: padding 0.3s;
    @include box();
    @include rwd(pad) {
      padding: 1.5rem 0;
    }
    @include rwd(mobile) {
      padding: 0.5rem 0;
    }
    .contain__title {
      font-style: italic;
      font-size: 3rem;
      transition: font-size 0.3s;
      @include rwd(pad) {
        font-size: 2.4rem;
      }
      @include rwd(mobile) {
        font-size: 1.8rem;
      }
    }
    .background {
      font-size: 1.8rem;
      letter-spacing: 1.5px;
      line-height: 1.8;
      transition: font-size 0.3s;
      @include rwd(pad) {
        font-size: 1.5rem;
      }
      @include rwd(mobile) {
        font-size: 1.2rem;
        line-height: 1.5;
      }
      .background__link {
        transition: all 0.3s;
        &:hover {
          font-size: 2rem;
          color: $main_blue !important;
        }
      }
    }
    .skills {
      @include flex(row, wrap);
      .skills__li {
        padding: 20px 0;
        font-size: 1.8rem;
        flex: 1;
        max-width: 25%;
        min-width: 25%;
        transition: all 0.3s;
        transform-origin: left center;
        user-select: none;
        &:hover {
          transform: scale(1.1);
          color: $main_peach;
        }
        @include rwd(laptop) {
          max-width: 33.33%;
          min-width: 33.33%;
        }
        @include rwd(pad) {
          max-width: 50%;
          min-width: 50%;
          font-size: 1.5rem;
        }
        @include rwd(mobile) {
          max-width: 100%;
          min-width: 100%;
          font-size: 1.2rem;
        }
      }
    }
    .exp {
      .exp__item {
        padding: 10px 0;
        p {
          font-size: 2rem;
          @include rwd(pad) {
            font-size: 1.5rem;
          }
          @include rwd(mobile) {
            font-size: 1.2rem;
          }
        }
        ul {
          .exp__list {
            padding: 1rem 0;
            @include rwd(pad) {
              padding: 0.8rem 0;
            }
            @include rwd(mobile) {
              padding: 0.6rem 0;
            }
            .exp__title {
              @include flex();
              position: relative;
              font-size: 1.8rem;
              @include rwd(pad) {
                font-size: 1.2rem;
              }
              @include rwd(mobile) {
                font-size: 1rem;
              }
              .exp__title__num {
                position: absolute;
                top: 0;
                bottom: 0;
                left: -40px;
                margin: auto;
                color: #fff;
              }
            }
            .exp__des {
              font-size: 1.5rem;
              margin-top: 0.5rem;
              @include rwd(pad) {
                font-size: 1.2rem;
              }
              @include rwd(mobile) {
                font-size: 1rem;
              }
            }
          }
        }
      }
    }
  }
}
// animation
@keyframes mouseScroll {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes mouseScrollStatic {
  0% {
    transform: rotate(-45deg);
  }
  100% {
    transform: rotate(-45deg);
  }
}
@keyframes arrowScroll {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(15px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes flicker {
  0% {
    opacity: 1;
  }
  1% {
    opacity: 0;
  }
  3% {
    opacity: 1;
  }
  5% {
    opacity: 0;
  }
  6% {
    opacity: 1;
  }
  // 100% {
  //   opacity: 0;
  // }
}
@keyframes flicker2 {
  27% {
    opacity: 1;
  }
  30% {
    opacity: 0;
  }
  32% {
    opacity: 1;
  }
  81% {
    opacity: 1;
  }
  82% {
    opacity: 0;
  }
  84% {
    opacity: 1;
  }
  // 100% {
  //   opacity: 0;
  // }
}
