@mixin flex($type: row, $wrap: nowrap) {
  display: flex;
  flex-flow: $type $wrap;
}
@mixin align($direction: flex-start) {
  align-items: $direction;
}
@mixin justify($direction: flex-start) {
  justify-content: $direction;
}
@mixin box {
  box-sizing: border-box;
}

@mixin rwd($device) {
  @if $device == laptop {
    @media screen and (max-width: 1200px) {
      @content;
    }
  } @else if $device == pad {
    @media screen and (max-width: 768px) {
      @content;
    }
  } @else if $device == mobile {
    @media screen and (max-width: 578px) {
      @content;
    }
  }
}
